import React from 'react';
// import PropTypes from 'prop-types';
// import styles from './MainMenu.module.scss';
import { faExpandArrowsAlt, faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const MainMenu = () => (
  <ul className="navbar-nav ml-auto right_side_menu">
    <li className="nav-item">
      <div className="input-group">
      <div className="input-group-prepend">
        <div className="input-group-text">
        <FontAwesomeIcon icon={faSearch} className="form_control_feedback"/>
        </div>
      </div>
      <input type="text" className="form-control custom_search" placeholder="Search Topic.." />
    </div>
    </li>
    <li className="nav-item full_screen">
      <FontAwesomeIcon icon={faExpandArrowsAlt} className="full_screen_icon"/>
    </li>
    {/* <li className="nav-item">
      <a className="nav-link" href="#">Link</a>
    </li> */}
  </ul>
);

MainMenu.propTypes = {};

MainMenu.defaultProps = {};

export default MainMenu;
