  var obj = keywordsdata[0].field;
  

  var linked_element_svg = document.getElementById('svg');
  var rect_svg = linked_element_svg.getBoundingClientRect();
  for (let iterator=0; iterator<obj.length; iterator++){
    const nodes = [];
    // console.log (obj[iterator]['id']);
    // console.log (obj[iterator]['linked_field_id']);
    
    var linked_element_start = document.getElementById('keyword_field_tick'+obj[iterator]['id']);
    var rect0 = linked_element_start.getBoundingClientRect();
    nodes.push([
      rect0.x - rect_svg.x*0.97, 
      (rect0.y - rect_svg.y)/0.95
    ]);
    obj[iterator]['linked_field_id'].forEach((current_jterator, i) => {
      // console.log(current_jterator);
      var current_linked_element = document.getElementById('topic'+current_jterator);
      var current_rect = current_linked_element.getBoundingClientRect();
      nodes.push([
        current_rect.x - rect_svg.x*0.97,
        (current_rect.y - rect_svg.y)/0.96,
      ]);
      let links = [];
      // console.log(nodes);
      links.push(
        d3.linkVertical()({
          source: nodes[0],
          target: nodes[i+1]
        })
      );
      for (let i = 0; i < links.length; i++) {
        svg.append('path')
          .attr('d', links[i])
          .attr('stroke', '#fff')
          .attr('fill', 'none')
          .attr('class', 'links keyword_link'+obj[iterator]['id']).lower();
        }
    });
  }

  
