import React from 'react';
// import PropTypes from 'prop-types';
import styles from './Banner.module.scss';
import { faShareSquare } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFacebook, faLinkedin, faTwitter } from "@fortawesome/free-brands-svg-icons"

const Banner = () => (
  <div className={styles.Banner}>
    <div className={styles.banner_image}>
      <img src={require('../../assets/banner.jpg').default} alt="banner" />
      <div className={styles.banner_content}>
        <span className={styles.category_name}>Category Name</span>
        <h1>Topic Heading</h1>
        {/* <h5>Curation: something based on which the current topic is being organized</h5> */}
        <h5>Curation: keyword relation</h5>
        <div className={styles.banner_footer}>
          <hr/>
          <div className={`tooltipa ${styles.share_icon}`}>
            <FontAwesomeIcon icon={faShareSquare}/>
            <div className='classic'>
              <ul>
                <li><a href="/"><FontAwesomeIcon icon={faFacebook}/> <span>Facebook</span></a></li>
                <li><a href="/"><FontAwesomeIcon icon={faLinkedin}/> <span>Linkedin</span></a></li>
                <li><a href="/"><FontAwesomeIcon icon={faTwitter}/> <span>Twitter</span></a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Banner;
